import { Component, OnInit, NgZone } from '@angular/core';
import {AuthenticationService} from '../../services/authentication/authentication.service';
import {NotificationsService} from '../../services/notifications/notifications.service';
import {MusicianService} from '../../services/musician/musician.service';
import {GigService} from '../../services/gig/gig.service';
import { ToastController, ModalController,AlertController } from '@ionic/angular';
import {FiltersPage} from '../filters/filters.page';
import {NotificationsPage} from '../notifications/notifications.page';
import { Router, RouterEvent, NavigationEnd } from '@angular/router';
import * as moment from 'moment';

@Component({
  selector: 'app-musicians',
  templateUrl: './musicians.page.html',
  styleUrls: ['./musicians.page.scss'],
})
export class MusiciansPage implements OnInit {

    public categories:Array<any>;
    
    public genres:Array<any>;
    public occasions:Array<any>;
    
    public popular:Array<any>;
    public new:Array<any>;
    public recent:Array<any>;
    public searchedMusicians:Array<any>;
    
    public notificationsCount:number;   
    public requests:Array<any>;
    
    public search:string;
    public filters:any;
    public loading:boolean;
    
 
    constructor(public toastController: ToastController, public modalController: ModalController,
        private router: Router, private authenticationService:AuthenticationService, private alertCtrl: AlertController, 
        private notificationsService:NotificationsService, private ngZone: NgZone, private musicianService: MusicianService, 
        private gigService: GigService) { 
        
        this.filters = {orderby:"created_at", order:"asc", instruments:""};
        this.search = "";  
        
        
        this.loading = true;
        
        //get genres
        this.genres = [];
        this.musicianService.getCategories(1).then((data:Array<any>) => {
            this.loading = false;
            this.genres = data;
        });
                
        //get occasions
        this.occasions = [];
        this.musicianService.getCategories(2).then((data:Array<any>) => {
            this.loading = false;
            this.occasions = data;
        });
        
        
            
        this.popular = [];
        this.new = [];
        this.recent = [];    
        this.searchedMusicians = [];    
        

        
        this.requests = [];
        
        
        
        this.notificationsCount = 0;
        this.notificationsService.monitorNotifications().subscribe((changed) => {

            this.notificationsService.getNotificationCount().then((data:number) => {
                this.ngZone.run(() => {  
                    if (this.notificationsCount !== data){
                        this.notificationsCount = data;
                        this.getBookingRequests();
                    }
                });
            })
        });           
        
    
    
    }

    ngOnInit() {
     
    }
    
    ionViewDidEnter(){
        
        this.getMusicians();

        this.getBookingRequests();        
    }    
    
    public getBookingRequests(){
        this.gigService.getBookingRequests().then((data:any) => {
            this.ngZone.run(() => {
                this.requests = data;
            });
            
        })          
    }
    
    public getMusicians(){
        this.musicianService.getMusicians(this.search, this.filters).then((data:any) => {
            console.log(data);
            this.loading = false;
            
            this.popular = data["popular"];
            this.new = data["new"];
            this.recent = data["recent"];
            
        })        
    }
    
    
    public doSearch(){
        this.musicianService.getMusicians(this.search, this.filters).then((data:Array<any>) => {
            this.searchedMusicians = data;       
        })
    }    
       
    
    public async openFilters(){
        const modal = await this.modalController.create({
          component: FiltersPage,
          componentProps: {filters:this.filters}
        });
        
        modal.onDidDismiss()
            .then((data:any) => {
                if (data && data.data){
                    this.filters = data.data.filters;
                    if (this.search){
                        this.doSearch();
                    }
                    else{
                        this.getMusicians(); 
                    }
                                  

                } 
            });        
        
        
        
        await modal.present();         
    } 
    
    
    public async openNotifications(){
        const modal = await this.modalController.create({
          component: NotificationsPage,
          componentProps: {}
        });

        await modal.present();         
    }    
    
    public calculateFeedback(musician){
        let rating = 100;
        
        if (musician.reviews){
            let reviewTotal = 0;
            for (let review of musician.reviews){
                
                reviewTotal = reviewTotal + (review.rating*20);
                
                
            }
            
            
            reviewTotal = (reviewTotal / musician.reviews.length);
            
            rating = reviewTotal;
        
        }
        
        return rating.toFixed(0);
        
    }
    
    public formatDate(date){
        let localeOffset = -(new Date().getTimezoneOffset());
        return moment(date).add(localeOffset, 'm').format("DD/MM/YYYY, h:mma");
    }       
    
     
    
    

}
